import { addData, changeData } from '../firebase/dataBase';

const initialState = [];

export default function items(state = initialState, action) {
	if (action.type === 'FETCH_DATA_SUCCESS') {
		return action.payload;
	}
	if (action.type === 'ADD_ITEM') {
		console.log(action.payload);
		addData(action.payload);
		return [
			...state,
			action.payload
		]
	}
	if (action.type === 'TOOGLE_CHECK') {
		changeData(action.payload);
		return state.map((item) => (item.id !== action.payload) ? item : {...item, done: !item.done})
	}
	if (action.type === 'FILTER_ITEMS') {
		let fieldName = action.payload.replace('-', '');
		console.log(fieldName);
		let reversed = action.payload[0] === '-';
		console.log(reversed);
		let ret = state.map(item => item).sort((a, b) => {
			let aF = a[fieldName];
			let bF = b[fieldName];
			return aF > bF ? 1 : (aF === bF ? 0 : -1);
		});
		let reversArr = ret.map(item => item).reverse();
		return state = (reversed ? reversArr : ret);
	}
	return state;
}


