import React, { Component } from 'react';
import { connect } from 'react-redux';

class Form extends Component {
	addItem = (event) => {
		event.preventDefault();
		const title = this.titleInput.value;
		const priority = this.priorityInput.value;
		const date = this.dateInput.value;
		const data = {
			title, priority, date, done: false, id: null
		};
		this.props.onAddItem(data);
		this.titleInput.value = '';
		this.priorityInput.value = '1';
		this.dateInput.value = '';
	};
	render() {
		return (
			<form id="toDoForm">
				<input type="text" id="item_title" ref={(input) => {this.titleInput = input}} />
				<select id="item_importance" size="1" ref={(input) => {this.priorityInput = input}}>
					<option value="1">высокая важность</option>
					<option value="2">средняя важность</option>
				</select>
				<input type="date" id="item_date" ref={(input) => {this.dateInput = input}} />
				<button id="add_item" onClick={this.addItem}>Добавить задание</button>
			</form>
		)
	}
}

export default connect(
	state => ({
		store: state.items
	}),
	dispatch => ({
		onAddItem: (data) => {
			dispatch({type: 'ADD_ITEM', payload: data})
		}
	})
)(Form);


