import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getData } from '../firebase/dataBase';
import 'font-awesome/css/font-awesome.min.css';

const columns = [{id: 1, name: 'done'}, {id: 2, name: 'title'}, {id: 3, name: 'priority'}, {id: 4, name: 'date'}];

const TableItem = ( {id, done, title, date, priority, onToogleCheck}) => (
	<tr key={id} className={priority === '1' ? 'important' : 'not_important'}>
		<td><input type="checkbox" defaultChecked={done} onChange={() => onToogleCheck(id)} /></td>
		<td>{title}</td>
		<td>{priority === '1' ? 'высокая важность' : 'средняя важность'}</td>
		<td>{date}</td>
	</tr>
);

const TableHead = ({onSort, name}) => (
	<th id={name}>{name} <button id="btnUp" onClick={() => onSort(name)}><i className="fa fa-chevron-up"/></button><button id="btnDawn" onClick={() => onSort(`-${name}`)}><i className="fa fa-chevron-down"/></button></th>
);

class Table extends Component {

	componentWillMount() {
		this.props.onGetData();
	}

	handleSort = (sortBy) => {
		this.props.onFilterItems(sortBy);
	};

	handleCheck = (id) => {
		this.props.onToogleCheck(id);
	};

	render() {
		return (
			<table>
				<thead>
				<tr>
					{columns.map((column) => <TableHead key={column.id} {...column} onSort={this.handleSort} />)}
				</tr>
				</thead>
				<tbody>
				{this.props.store.map((item, index) => {
					return (
						<TableItem key={index} {...item} onToogleCheck={this.handleCheck} />
					)
				})}
				</tbody>
			</table>
		)
	}
}

export default connect(
	state => ({
		store: state.items.filter(item => {
			if (item.title.includes(state.findItem.content) && new Date(item.date) >= new Date(state.findItem.dateFrom) && new Date(item.date) <= new Date(state.findItem.dateTo) && (state.findItem.showDone ? true : (state.findItem.showDone === item.done))) {
				return item;
			}})
	}),
	dispatch => ({
		onFilterItems: (data) => {
			dispatch({type: 'FILTER_ITEMS', payload: data})
		},
		onToogleCheck: (data) => {
			dispatch({type: 'TOOGLE_CHECK', payload: data})
		},
		onGetData: () => {
			dispatch(getData());
		}
	})
)(Table);


