const initialState = {
	content: '',
	dateFrom: '2012-10-10',
	dateTo: '2020-10-10',
	showDone: true
};

export default function findItem(state = initialState, action) {
	if (action.type === 'FIND_ITEM') {
		if (!action.payload.dateFrom) {
			action.payload.dateFrom = '2012-10-10'
		}
		if (!action.payload.dateTo) {
			action.payload.dateTo = '2020-10-10'
		}
		return action.payload
	}
	return state
}


