import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getData } from '../firebase/dataBase';
import './Calendar.css';

class Task extends Component {
	render() {
		return (
			<div>
				<p className={this.props.priority === '1' ? 'important' : 'not_important'}>{this.props.title}</p>
			</div>
		)
	}
}

class Header extends Component {
	render() {
		return (
			<div className="r-row r-head">
				<div className="r-cell r-prev" onClick={this.props.onPrev} role='button' tabIndex='0'/>
				<div className='r-cell r-title'>{this.props.monthNames[this.props.month] + ' ' + this.props.year}</div>
				<div className='r-cell r-next' onClick={this.props.onNext} role='button' tabIndex='0'/>
			</div>
		)
	}
}

class WeekDays extends Component {
	render() {
		let that = this,
			haystack = Array.apply(null, { length: 7 }).map(Number.call, Number);
		return (
			<div className="r-row r-weekdays">
				{
					(() => {
						if (that.props.weekNumbers) {
							return (
								<div className="r-cell r-weeknum">wn</div>
							);
						}
					})()
				}
				{
					haystack.map((item, i) => {
						return (
							<div key={i} className="r-cell">{that.props.dayNames[(that.props.startDay + i) % 7]}</div>
						)
					})
				}
			</div>
		);
	}
}

class MonthDates extends Component {

	render() {
		let haystack,
			day,
			d,
			current,
			isDate,
			className,
			weekStack = Array.apply(null, { length: 7 }).map(Number.call, Number),
			that = this,
			startDay = this.props.firstOfMonth.getUTCDay(),
			first = this.props.firstOfMonth.getDay(),
			janOne = new Date(that.props.year, 0, 1),
			rows = 5;

		if (startDay === 5 && this.props.daysInMonth === 31 || startDay === 6 && this.props.daysInMonth > 29) {
			rows = 6;
		}

		className = rows === 6 ? 'r-dates' : 'r-dates r-fix';
		haystack = Array.apply(null, { length: rows }).map(Number.call, Number);
		day = this.props.startDay + 1 - first;
		while (day > 1) {
			day -= 7;
		}
		day -= 1;
		return (
			<div className={className}>
				{
					haystack.map(function (item, i) {
						d = day + i * 7;
						return (
							<div key={i} className="r-row">
								{
									(() => {
										if (that.props.weekNumbers) {
											let wn = Math.ceil(((new Date(that.props.year, that.props.month, d) - janOne) / 86400000 + janOne.getDay() + 1) / 7);
											return (
												<div className="r-cell r-weeknum">{wn}</div>
											)
										}
									})()
								}
								{
									weekStack.map((i) => {
										d += 1;
										isDate = d > 0 && d <= that.props.daysInMonth;

										if (isDate) {
											let date = that.props.year + '-' + that.props.month + '-' + d;
											let today = new Date().getFullYear() + '-' + new Date().getMonth() + '-' + new Date().getDate();
											className = today !== date ? 'r-cell r-date' : 'r-cell r-date r-today';
											return (
												<div key={i} className={className} role='button' tabIndex='0' >
													{d}
													<div className="task">
														{
															that.props.store.map(item => {
																let newD;
																let newMonth;
																if (d < 10) {
																	newD = '0' + d;
																} else {
																	newD = d;
																}
																newMonth = that.props.month + 1;
																if (newMonth < 10) {
																	newMonth = '0' + newMonth;
																} else {
																	newMonth = newMonth;
																}
																let date = that.props.year + '-' + newMonth + '-' + newD;
																if (item.date === date) {
																	return(
																		<Task title={item.title} priority={item.priority} />
																	)
																}
															})
														}
													</div>
												</div>
											);
									}
									return (
										<div className="r-cell"></div>
									)
								})
								}
							</div>
						);
					})
				}
			</div>
		);
	}
}

class Calendar extends Component {

	componentWillMount() {
		this.setState(this.calc.call(null, this.state.year, this.state.month));
		this.props.onGetData();
	};

	componentDidMount () {};

	componentDidUpdate(prevState) {
		if (this.props.onSelect && prevState.selectedDt !== this.state.selectedDt) {
			this.props.onSelect.call(this.getDOMNode(), this.state);
		}
	};

	constructor() {
		let date = new Date();
		super();
		this.state = {
			year: date.getFullYear(),
			month: date.getMonth(),
			selectedYear: date.getFullYear(),
			selectedMonth: date.getMonth(),
			selectedDate: date.getDate(),
			selectedDt: new Date(date.getFullYear(), date.getMonth(), date.getDate()),
			startDay: 1,
			weekNumbers: false,
			dayNames: ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'],
			monthNamesFull: ["Январь", "Февраль", "Март", "Апрель", "Май", "Июнь", "Июль", "Август", "Сентябрь", "Октябрь", "Ноябрь", "Декабрь"],
			firstOfMonth: null,
			daysInMonth: null
		}
	}

	calc = (year, month) => {
		if (this.state.selectedElement) {
			if (this.state.selectedMonth !== month || this.state.selectedYear !== year) {
				this.state.selectedElement.classList.remove('r-selected');
			} else {
				this.state.selectedElement.classList.add('r-selected');
			}
		}
		return {
			firstOfMonth: new Date(year, month, 1),
			daysInMonth: new Date(year, month + 1, 0).getDate()
		};
	};

	getPrev = () => {
		let state = {};
		if (this.state.month > 0) {
			state.month = this.state.month - 1;
			state.year = this.state.year;
		} else {
			state.month = 11;
			state.year = this.state.year - 1;
		}
		Object.assign(state, this.calc.call(null, state.year, state.month));
		this.setState(state);
	};

	getNext = () => {
		let state = {};
		if (this.state.month < 11) {
			state.month = this.state.month + 1;
			state.year = this.state.year;
		} else {
			state.month = 0;
			state.year = this.state.year + 1;
		}
		Object.assign(state, this.calc.call(null, state.year, state.month));
		this.setState(state);
	};

	render() {
		return (
			<div id="calendar" className="r-calendar">
				<div className="r-inner">
					<Header monthNames={this.state.monthNamesFull} month={this.state.month} year={this.state.year} onPrev={this.getPrev} onNext={this.getNext} />
					<WeekDays dayNames={this.state.dayNames} startDay={this.state.startDay} weekNumbers={this.state.weekNumbers} />
					<MonthDates store={this.props.store} month={this.state.month} year={this.state.year} daysInMonth={this.state.daysInMonth} firstOfMonth={this.state.firstOfMonth} startDay={this.state.startDay} weekNumbers={this.state.weekNumbers} disablePast={this.state.disablePast} minDate={this.state.minDate} />
				</div>
			</div>
		);
	}
}

export default connect(
	state => ({
		store: state.items
	}),
	dispatch => ({
		onGetData: () => {
			dispatch(getData());
		}
	})
)(Calendar);


