import React, { Component } from 'react';
import { connect } from 'react-redux';

class Filter extends Component {

	findItem = () => {
		const content = this.contentInput.value;
		const dateFrom = this.dateFromInput.value;
		const dateTo = this.dateToInput.value;
		const showDone = this.showDoneInput.checked;
		this.props.onFindItem(content, dateFrom, dateTo, showDone);
	};

	render() {
		return (
			<form id="toDoFilter" onChange={this.findItem}>
				<p>Показывать выполненные</p>
				<input id="showChecked" type="checkbox" ref={(input) => {this.showDoneInput = input}} defaultChecked />
				<p>Фильтрация по содержанию</p>
				<input id="filterText" type="text" ref={(input) => {this.contentInput = input}} />
				<p>Фильтрация по дате</p>
				<input id="dateFrom" type="date" ref={(input) => {this.dateFromInput = input}} />
				<input id="dateTo" type="date" ref={(input) => {this.dateToInput = input}} />
			</form>
		)
	}
}

export default connect(
	state => ({
		store: state.items
	}),
	dispatch => ({
		onFindItem: (content, dateFrom, dateTo, showDone) => {
			const data = {
				content, dateFrom, dateTo, showDone
			};
			dispatch({type: 'FIND_ITEM', payload: data})
		}
	})
)(Filter);


